import { createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`
 @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap'); // wzite z google fonts


 html { // border-box jest znacznie bardziej przewidywalny ni content-Box
  box-sizing: border-box;
 }

 *, *::after, *::before { // by wszystkie elementy i pseudoelementy dziedziczyy box-sizing
  box-sizing: inherit;
 }

 body {
  font-family: 'Montserrat', sans-serif;
 }

 a, button { // linki i buttony nie dziedzicz z body font-family
  font-family: 'Montserrat', sans-serif;
 }
`;
